@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.ofertas{
    margin-bottom: v.$separacion;
    //min-height: 45vh;
    h1{
        margin: 5rem 0;
    }
}

.btn-volver{
    @include m.boton(v.$azul, inline-block);
}

.btn-vacante{
    @include m.boton(transparent, block);
    background-image: linear-gradient(165deg, v.$azulClaro, v.$azul 75%);
    //@include m.boton(v.$asignar, block);
    border-radius: 0 0 .5rem .5rem ;
    &:hover{
        background-image: linear-gradient(-35deg, v.$azulClaro 75%, v.$azul);
    }    
}

.ofertas-nav{
    margin-bottom: v.$separacion;
    nav{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: flex-start;
        justify-items: center;
        gap: v.$separacion;

        a{
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .iconos{
            width: 75%;
            background: v.$asignar;
            text-align: center;
            padding: v.$separacion 0;
            box-shadow: 3px 3px 5px #707070;
            border-radius: 1rem;
            height: 100%;
            & p{
                font-size: 1.8rem;
                margin: 0;
                color: v.$blanco;
                text-transform: capitalize;
            }

            .fa{
                color: v.$blanco !important;
                
                font-size: 2.2rem !important;
            }

            &:hover{
                cursor: pointer;
                transform: scale(1.02);
                background-color: v.$verde;
            }
            @include m.tablet{
                display: flex;
                justify-content: center;
                align-items: center;
                & p{
                    padding: 0 1rem;
                    font-size: 3rem;
                }
                .fa{
                    font-size: 2.2rem !important;
                }
            }
        }
    }
}

.vacantes{
    margin: v.$separacion auto;
    span{
        text-transform: capitalize;
    }
    .sin-ofertas{
        background-color: v.$gris;
        gap: v.$separacion;
        width: 70%;
        margin: auto;
        margin-top: v.$separacion;
        padding: 1.5rem 3rem;
        border-radius: 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        i, p{
            color: v.$blanco;
            font-weight: v.$bold;
            text-align: center;
        }
        i{
            font-size: 8rem;
        }
        p{
            font-size: 4rem;
        }
    }
}

.grid-vacantes{
    @include m.grid(6, 2rem);    
}

.vacante{
    //border: 3px solid v.$asignar;
    box-shadow: 4px 4px 0 v.$azul,  4px -4px 0 v.$azulClaro,-4px -4px 0 v.$azulClaro, -4px 4px 0 v.$azul;
    border-radius: 1rem;
    font-size: 0px;
    display: flex;
    flex-direction: column;
    img{
        border-radius: .7rem .7rem 0 0;
        object-fit: cover;            
    }
    p{
        padding: 1rem;
        margin: auto;
        span{
            font-weight: v.$medium;
            font-size: 2.2rem;
        }
    }
    a{
        font-size: 1.8rem;
    }
}

.top{
    h1{
        text-transform: capitalize;
    }
}

.info-oferta{
    padding: v.$separacion;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: rgba(255, 255, 255, 0.8);
    // background-color: lighten(v.$grisClaro, 9);
    background-blend-mode: lighten;
    border-radius: 1rem;
    margin-bottom: v.$separacion;
    
    @include m.grid(2, 0);
    place-items: center;

    img{
        max-width: 50rem;
        border-radius: 1rem;
        //border: 5px solid v.$asignar;
        box-shadow: 5px 5px 5px v.$gris;
    }
    p{
        font-size: 2.2rem;
        font-weight: v.$medium;
        text-transform: capitalize;
    }
    span{
        font-weight: v.$bold;
        color: v.$asignar;
        text-transform: capitalize;
    }
}